import { IconButton, Snackbar } from "@mui/material";
import React, { Dispatch, SetStateAction, useEffect, useState } from "react";
import CloseIcon from "@mui/icons-material/Close";
import "./csnackbar.scss";

interface CSnackBarProps {
  isSnackBarOpened: boolean;
  duration: number;
  message: React.ReactNode;
  setIsSnackBarOpened: Dispatch<SetStateAction<boolean>>;
}

export const CSnackBar: React.FunctionComponent<CSnackBarProps> = (
  props: CSnackBarProps
) => {
  const snackBarAction = (
    <React.Fragment>
      <IconButton
        size="small"
        aria-label="close"
        color="inherit"
        onClick={() => props.setIsSnackBarOpened(false)}
      >
        <CloseIcon fontSize="small" />
      </IconButton>
    </React.Fragment>
  );
  return (
    <>
      <Snackbar
        className="csnackbar"
        open={props.isSnackBarOpened}
        autoHideDuration={props.duration}
        onClose={() => props.setIsSnackBarOpened(false)}
        message={props.message}
        action={snackBarAction}
        sx={{ backgroundColor: "#000" }}
      />
    </>
  );
};
