import React, { useState } from "react";
import FolderIcon from "@mui/icons-material/Folder";
import CrisisAlertIcon from "@mui/icons-material/CrisisAlert";
import AssignmentIcon from "@mui/icons-material/Assignment";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import KeyboardArrowUpIcon from "@mui/icons-material/KeyboardArrowUp";
import { Button, Chip, Grid, IconButton, Paper, Tooltip } from "@mui/material";
import { Experience } from "../../datatypes/bindings";
import { CSnackBar } from "../../shared/components/csnackbar/csnackbar";
import { CDialog } from "../../shared/components/cdialog/cdialog";
import "./experience.scss";

interface ExperienceProps {
  experiences?: Experience[];
  setFilter: (projectName: string, projectTechs: string) => void;
}

export const CExperience: React.FunctionComponent<ExperienceProps> = (
  props: ExperienceProps
) => {
  const [openSnackBar, setOpenSnackBar] = useState(false);
  const [openExpDialog, setOpenExpDialog] = useState(false);
  const [seeMore, setSeeMore] = useState(false);
  const [selectedTech, setSelectedTech] = useState<Experience>();
  const findInProjects = (exp: Experience) => {
    props.setFilter("", exp.tech);
    setSelectedTech(exp);
    window.location.href = "#project-container";
    setOpenSnackBar(true);
  };

  const showExpTech = (exp: Experience) => {
    setSelectedTech(exp);
    setOpenExpDialog(true);
  };

  const getTechItems = (): Experience[] | undefined => {
    return seeMore ? props.experiences : props.experiences?.slice(0, 8);
  };

  const getTechIcon = (exp: Experience) => {
    return (
      <Grid
        key={"experience-" + exp.tech + "-grid"}
        item
        xs={12}
        sm={6}
        md={3}
        lg={3}
        xl={3}
      >
        <Paper
          key={"experience-" + exp.tech + "-paper"}
          className="tech-container"
          elevation={1}
          sx={{
            padding: "20px",
            backgroundColor: exp.mainStack ? "#fff" : "#121212",
          }}
        >
          <div
            key={"experience-" + exp.tech + "-img"}
            className="tech-icon"
            style={{
              width: "100%",
              height: "200px",
              backgroundImage: `url("${exp.img}")`,
              backgroundSize: "contain",
              backgroundPosition: "center",
              backgroundRepeat: "no-repeat",
            }}
          ></div>
          <p key={"experience-" + exp.tech + "-name"}>
            <b
              key={"experience-" + exp.tech + "-name-b"}
              style={{ color: exp.mainStack ? "#000" : "#fff" }}
            >
              {exp.tech}
            </b>
          </p>
          <Grid
            key={"experience-" + exp.tech + "-actions"}
            container
            spacing={0}
          >
            <Grid
              key={"experience-" + exp.tech + "-grid"}
              item
              xs={6}
              sm={6}
              md={2}
              lg={2}
              xl={2}
              textAlign="right"
            >
              <Tooltip
                key={"experience-tooltip-project-" + exp.tech}
                title="Find projects"
              >
                <IconButton
                  key={"experience-tooltip-project-" + exp.tech}
                  sx={{ color: exp.mainStack ? "#000" : "#fff" }}
                  onClick={() => findInProjects(exp)}
                >
                  <FolderIcon />
                </IconButton>
              </Tooltip>
            </Grid>
            <Grid
              key={"experience-more-" + exp.tech}
              item
              xs={6}
              sm={6}
              md={2}
              lg={2}
              xl={2}
            >
              <Tooltip
                key={"experience-more-tooltip-" + exp.tech}
                title="See experience"
              >
                <IconButton
                  key={"experience-more-icon-btn-" + exp.tech}
                  sx={{ color: exp.mainStack ? "#000" : "#fff" }}
                  onClick={() => showExpTech(exp)}
                >
                  <AssignmentIcon />
                </IconButton>
              </Tooltip>
            </Grid>
            <Grid
              key={"experience-years-" + exp.tech}
              item
              xs={12}
              sm={12}
              md={8}
              lg={8}
              xl={8}
              sx={{ paddingX: "10px" }}
              textAlign="right"
              alignContent="center"
            >
              <Tooltip
                key={"experience-years-tooltip-" + exp.tech}
                title="Years of experience"
              >
                <Chip
                  key={"experience-years-chip-" + exp.tech}
                  label={`${exp.yearsOfExperience} years`}
                  size="small"
                  sx={{
                    width: "100%",
                    backgroundColor: exp.mainStack
                      ? "#000"
                      : "rgba(255, 255, 255, 0.16)",
                  }}
                />
              </Tooltip>
            </Grid>
          </Grid>
        </Paper>
      </Grid>
    );
  };

  return (
    <div key="experience-container" className="experience-container">
      <Grid key="experience-grid" container spacing={2}>
        {getTechItems()?.map((exp) => getTechIcon(exp))}
        <Grid
          key={"experience-more-grid"}
          item
          xs={12}
          sm={12}
          md={12}
          lg={12}
          xl={12}
          textAlign="center"
        >
          <Button
            key="experience-see-more"
            startIcon={
              seeMore ? (
                <KeyboardArrowUpIcon
                  key="experience-up-see-more"
                  sx={{ color: "#90caf9" }}
                />
              ) : (
                <KeyboardArrowDownIcon
                  key="experience-down-see-more"
                  sx={{ color: "#90caf9" }}
                />
              )
            }
            onClick={() => setSeeMore(!seeMore)}
            variant="outlined"
            size="large"
            sx={{ marginTop: "20px" }}
          >
            {seeMore ? "See less" : "See more"}
          </Button>
        </Grid>
      </Grid>

      <CSnackBar
        key="experience-snack-bar"
        isSnackBarOpened={openSnackBar}
        setIsSnackBarOpened={setOpenSnackBar}
        duration={6000}
        message={
          <Button
            key="experience-snack-bar-btn"
            className="snack-message"
            startIcon={<CrisisAlertIcon className="snack-icon" />}
            disabled
          >
            L
            <span className="snack-message-span">
              ooking for projects with <b>{selectedTech?.tech}</b> technology
            </span>
          </Button>
        }
      />

      <CDialog
        key="experience-dialog"
        isDialogOpened={openExpDialog}
        setIsDialogOpened={setOpenExpDialog}
        title={<h2>My experience with {selectedTech?.tech}</h2>}
        message={
          <nav key="experience-dialog-nav">
            <ul key="experience-dialog-nav-ul" className="exp-list">
              {selectedTech?.details.split("/").map((p: string) => (
                <li key={"experience-" + p}>
                  <p key={"experience-" + p + "-desc"}>
                    {p.replace("*", "").trim()}
                  </p>
                </li>
              ))}
            </ul>
          </nav>
        }
      />
    </div>
  );
};
