import Header from "./header/Header";
import BodyComponent from "./body/Body";
import { useEffect, useMemo, useState } from "react";
import { ApiClient, ENV } from "./datatypes/apiClient";
import { LoadingState, Project, User } from "./datatypes/bindings";

import { ThemeProvider, createTheme } from "@mui/material/styles";

import "./App.scss";

const darkTheme = createTheme({
  palette: {
    mode: "dark",
  },
});

function App() {
  const apiClient: ApiClient = useMemo(() => new ApiClient(ENV.PROD), []);
  const [projects, setProjects] = useState([] as Project[]);
  const [user, setUser] = useState({});
  const [experiences, setExperiences] = useState();

  const [projectsLoaded, setProjectsLoaded] = useState(LoadingState.NOT_LOADED);
  const [userLoaded, setUserLoaded] = useState(LoadingState.NOT_LOADED);
  const [experiencesLoaded, setExperiencesLoaded] = useState(
    LoadingState.NOT_LOADED
  );

  useEffect(() => {
    if (projectsLoaded === LoadingState.NOT_LOADED) {
      setProjectsLoaded(LoadingState.LOADING);
      apiClient.getProjects().then((response) => {
        response.json().then((projects) => {
          setProjects(projects);
          setProjectsLoaded(LoadingState.LOADED);
        });
      });
    }
    if (userLoaded === LoadingState.NOT_LOADED) {
      setUserLoaded(LoadingState.LOADING);
      apiClient.getUser().then((response) => {
        response.json().then((user) => {
          setUser(user);
          setUserLoaded(LoadingState.LOADED);
        });
      });
    }
    if (experiencesLoaded === LoadingState.NOT_LOADED) {
      setExperiencesLoaded(LoadingState.LOADING);
      apiClient.getExperience().then((response) => {
        response.json().then((experiences) => {
          setExperiences(experiences);
          setExperiencesLoaded(LoadingState.LOADED);
        });
      });
    }
  }, [apiClient, projectsLoaded, userLoaded]);

  const setFilters = (projectName: string, projectTechs: string) => {
    setProjectsLoaded(LoadingState.LOADING);
    const techsArray = projectTechs
      .toLowerCase()
      .split(",")
      .map((tech) => tech.trim())
      .filter((tech) => tech !== "" && tech !== undefined);
    apiClient
      .getProjectsByQuery(
        projectName !== "" && projectName !== undefined
          ? projectName
          : undefined,
        techsArray.length > 0 ? techsArray : undefined
      )
      .then((response) =>
        response.json().then((projects) => {
          setProjectsLoaded(LoadingState.LOADED);
          setProjects(projects);
        })
      )
      .catch(() => setProjectsLoaded(LoadingState.LOADED));
  };

  return (
    <ThemeProvider theme={darkTheme}>
      <div className="App">
        <Header />
        <BodyComponent
          apiClient={apiClient}
          user={user as User}
          projectLoadedState={projectsLoaded}
          projects={projects}
          experiences={experiences}
          setFilter={setFilters}
        />
      </div>
    </ThemeProvider>
  );
}

export default App;
