import { Mail, Updates } from "./bindings";

export enum ENV {
  LOCAL,
  PROD,
}

export class ApiClient {
  private baseURL: string;
  private baseLocalURL: string = "http://localhost:8080";
  private baseProdURL: string = "http://rrgzdev.us-east-1.elasticbeanstalk.com";
  private headers: { [header: string]: string } = {
    "content-type": "application/json;charset=UTF-8",
  };

  constructor(env: ENV) {
    if (env === ENV.LOCAL) {
      this.baseURL = this.baseLocalURL;
    } else {
      this.baseURL = this.baseProdURL;
    }
  }

  async getProjects(): Promise<Response> {
    return fetch(`${this.baseURL}/projects`, {
      method: "GET",
      headers: this.headers,
    });
  }

  async getUser(): Promise<Response> {
    return fetch(`${this.baseURL}/user/login`, {
      method: "POST",
      headers: this.headers,
      body: JSON.stringify({
        username: "Rene Rodriguez L",
        password: "pelemarley23",
      }),
    });
  }

  async getProjectsByQuery(
    projectName?: string,
    projectTechs?: string[]
  ): Promise<Response> {
    return fetch(`${this.baseURL}/projects/query`, {
      method: "POST",
      headers: this.headers,
      body: JSON.stringify({
        name: projectName,
        technologies: projectTechs,
      }),
    });
  }

  async getExperience(): Promise<Response> {
    return fetch(`${this.baseURL}/experience`, {
      method: 'GET',
      headers: this.headers,
    });
  }

  async getLastTenUpdates(): Promise<Response> {
    return fetch(`${this.baseURL}/updates`, {
      method: 'GET',
      headers: this.headers,
    });
  }
}
