import React, { useEffect, useState } from "react";
import Photo from "../../assets/imgs/photo2.png";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Grid,
  Paper,
} from "@mui/material";
import { ApiClient } from "../../datatypes/apiClient";
import { Updates } from "../../datatypes/bindings";
import "./bio.scss";

interface BioProps {
  apiClients: ApiClient;
}

export const Bio: React.FunctionComponent<BioProps> = (props: BioProps) => {
  const [updates, setUpdates] = useState<Updates[]>([]);
  const getAge = () => {
    const birthdayYear = 1998;
    const currentYear = new Date().getFullYear();
    return currentYear - birthdayYear;
  };
  const getPublishDays = (upd: Updates) => {
    const publishedTime: Date = new Date(upd.publishedTime);
    const today: Date = new Date();
    const millisecondsPerDay: number = 24 * 60 * 60 * 1000;
    return Math.floor(
      (Date.parse(today.toUTCString()) -
        Date.parse(publishedTime.toUTCString())) /
        millisecondsPerDay
    );
  };

  useEffect(() => {
    props.apiClients.getLastTenUpdates().then((resp) => {
      resp.json().then((upds: Updates[]) => {
        setUpdates(upds);
      });
    });
  }, []);

  return (
    <div className="bio-container">
      <Grid key="bio-grid" className="bio-grid" container spacing={0}>
        <Grid
          key="bio-myself-grid"
          item
          xs={12}
          sm={12}
          md={4}
          lg={4}
          xl={4}
          className="bio-grid-myself"
          padding="10px"
          textAlign="center"
        >
          <Paper elevation={1} sx={{ padding: "20px" }}>
            <Grid key="bio-myself-paper-grid" container item spacing={1}>
              <Grid
                key="bio-myself-paper-sub-grid"
                item
                xs={12}
                sm={6}
                md={12}
                lg={12}
                xl={12}
              >
                <img src={Photo} alt="profile photo" width="100%" />
              </Grid>
              <Grid
                key="bio-myself-paper-sub-about-grid"
                item
                className="about-info"
                xs={12}
                sm={6}
                md={12}
                lg={12}
                xl={12}
              >
                <h4>Ren Rodrguez Luquin</h4>
                <p>
                  <b>Age and gender: </b>
                  {getAge()}, Man
                </p>
                <p>
                  <b>Location: </b>Sonora, Mxico
                </p>
                <p>
                  <b>Education: </b>ITESCA, Computer System Engineering
                </p>
                <p>
                  <b>Occupation: </b>Full Stack Developer
                </p>
              </Grid>
            </Grid>
          </Paper>
        </Grid>
        <Grid
          key="bio-about-grid"
          className="bio-grid-about"
          item
          xs={12}
          sm={12}
          md={4}
          lg={4}
          xl={4}
          padding="10px"
        >
          <Paper className="paper-about" elevation={1} sx={{ padding: "20px" }}>
            <h4>About</h4>
            <p>
              I'm a technology lover, I've been coding since I was 8 years old,
              I've been involved in all the software development aspects,
              <b>designing</b>, <b>coding</b>, <b>architecture</b>,{" "}
              <b>testing</b>, <b>deployment</b> and more, I'm not afraid of
              taking new challenges, I like to learn and I learn pretty fast,
              basically a technopath
            </p>
          </Paper>
          <Paper className="paper-goals" elevation={1} sx={{ padding: "20px" }}>
            <h4>Goals</h4>
            <nav>
              <ul>
                <li>
                  <p>
                    Finding new and excited challenges for my professional
                    growth
                  </p>
                </li>
                <li>
                  <p>Exploring new technologies and software areas</p>
                </li>
                <li>
                  <p>
                    Learn and use my knowledge to create and implement excited
                    things
                  </p>
                </li>
              </ul>
            </nav>
          </Paper>
        </Grid>
        <Grid
          className="bio-grid-updates"
          item
          xs={12}
          sm={12}
          md={4}
          lg={4}
          xl={4}
          padding="10px"
        >
          <Paper elevation={1} sx={{ padding: "20px" }}>
            <h4>Last updates</h4>
            <div className="last-updates-container">
              {updates.map((upd: Updates) => (
                <Accordion
                  key={"bio-" + upd.title}
                  sx={{ backgroundColor: "black" }}
                >
                  <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                    <Grid container spacing={0}>
                      <Grid
                        item
                        xs={12}
                        sm={12}
                        md={12}
                        lg={12}
                        xl={12}
                        paddingRight="4%"
                      >
                        <small className="upd-days">
                          {getPublishDays(upd)} day ago
                        </small>
                      </Grid>
                      <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
                        {upd.title}
                      </Grid>
                    </Grid>
                  </AccordionSummary>
                  <AccordionDetails>{upd.description}</AccordionDetails>
                </Accordion>
              ))}
            </div>
          </Paper>
        </Grid>
      </Grid>
    </div>
  );
};
