import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from "@mui/material";
import { Dispatch, SetStateAction } from "react";
import "./cdialog.scss";

interface CDialogProps {
  isDialogOpened: boolean;
  title: React.ReactNode;
  message: React.ReactNode;
  setIsDialogOpened: Dispatch<SetStateAction<boolean>>;
}

export const CDialog: React.FunctionComponent<CDialogProps> = (
  props: CDialogProps
) => {
  return (
    <>
      <Dialog
        className="cdialog"
        open={props.isDialogOpened}
        onClose={() => props.setIsDialogOpened(false)}
        scroll="paper"
      >
        <DialogTitle style={{ backgroundColor: "#6aa4df" }}>
          {props.title}
        </DialogTitle>
        <DialogContent sx={{ backgroundColor: "#262626" }} dividers>
          <DialogContentText tabIndex={-1}>{props.message}</DialogContentText>
        </DialogContent>
        <DialogActions sx={{ backgroundColor: "#262626" }}>
          <Button onClick={() => props.setIsDialogOpened(false)}>Close</Button>
        </DialogActions>
      </Dialog>
    </>
  );
};
