import { Experience, LoadingState, Project, User } from "../datatypes/bindings";
import { Landing } from "./landing/landing";
import { Bio } from "./bio/bio";
import { CExperience } from "./experience/experience";
import { Timeline } from "./timeline/timeline";
import { CProject } from "./projects/projects";
import "./Body.scss";
import { ApiClient } from "../datatypes/apiClient";

interface BodyProps {
  apiClient: ApiClient;
  projects?: Project[];
  user?: User;
  experiences?: Experience[];
  projectLoadedState: LoadingState;
  setFilter: (projectName: string, projectTechs: string) => void;
}

const BodyComponent: React.FunctionComponent<BodyProps> = (
  props: BodyProps
) => {
  return (
    <>
      <main>
        <Landing key="landing" user={props.user} />
        <Bio key="bio" apiClients={props.apiClient} />
        <Timeline key="timeline" />
        <CExperience
          key="experiences"
          experiences={props.experiences}
          setFilter={props.setFilter}
        />
        <CProject
          key="project"
          projects={props.projects}
          projectLoadedState={props.projectLoadedState}
          setFilter={props.setFilter}
        />
      </main>
    </>
  );
};

export default BodyComponent;
