import React, { useState } from "react";
import LinkIcon from "@mui/icons-material/Link";
import SearchIcon from "@mui/icons-material/Search";
import { LoadingState, Project } from "../../datatypes/bindings";
import {
  Chip,
  CircularProgress,
  Grid,
  IconButton,
  Paper,
  TextField,
} from "@mui/material";
import "./projects.scss";

interface CProjectProps {
  projects?: Project[];
  projectLoadedState: LoadingState;
  setFilter: (projectName: string, projectTechs: string) => void;
}

export const CProject: React.FunctionComponent<CProjectProps> = (
  props: CProjectProps
) => {
  const [techFilter, setTechFilter] = useState<string>("");
  const updateTechFilter = (tech: string) => {
    setTechFilter(tech);
  };

  const getProjectPhoto = (name: string, img: string) => {
    return (
      <div
        key={"project-img-" + name}
        style={{
          width: "100%",
          height: "200px",
          margin: "20px 0px",
          borderRadius: "40px",
          backgroundImage: `url("${img}")`,
          backgroundSize: "cover",
          backgroundRepeat: "no-repeat",
          backgroundPosition: "center",
          borderRight: "6px solid #6aa4df",
          borderBottom: "6px solid #6aa4df",
          boxShadow: "10px 10px 27px 0px rgba(95, 147, 199, 0.33)",
        }}
      ></div>
    );
  };
  return (
    <div id="project-container">
      <h2>Projects</h2>
      <TextField
        label="Technology"
        placeholder="Javascript, typescript, ..."
        InputProps={{
          endAdornment: (
            <IconButton onClick={() => props.setFilter("", techFilter)}>
              <SearchIcon />
            </IconButton>
          ),
        }}
        value={techFilter}
        sx={{ marginBottom: "30px" }}
        onChange={(e) => updateTechFilter(e.target.value)}
        onKeyDown={(e) => {
          if (e.key === "Enter") {
            props.setFilter("", techFilter);
          }
        }}
        fullWidth
      />

      <Grid container spacing={2}>
        {props.projectLoadedState !== LoadingState.LOADING &&
          props.projects?.map((p) => (
            <Grid
              key={"project-" + p.name}
              item
              xs={12}
              sm={12}
              md={6}
              lg={6}
              xl={6}
            >
              <Paper
                key={"projects-description-paper-" + p.name}
                elevation={1}
                sx={{ padding: "20px" }}
              >
                <Grid
                  key={"projects-description-grid-" + p.name}
                  container
                  item
                  xs={12}
                  sm={12}
                  md={12}
                  lg={12}
                  xl={12}
                >
                  <Grid
                    key={"projects-description-" + p.name}
                    item
                    xs={12}
                    sm={12}
                    md={6}
                    lg={6}
                    xl={6}
                    sx={{ paddingRight: "10px" }}
                  >
                    <p>
                      <b>{p.name}</b>
                    </p>
                    <p>{p.description}</p>
                    {p.url && (
                      <a
                        key={"projects-link-" + p.name}
                        className="project-link"
                        href={p.url}
                        target="_blank"
                      >
                        <Chip
                          key={"project-link-icon-" + p.name}
                          label="Open link"
                          color="success"
                          icon={<LinkIcon />}
                        />
                      </a>
                    )}
                  </Grid>
                  <Grid
                    key={"projects-photo-grid-" + p.name}
                    item
                    xs={12}
                    sm={12}
                    md={6}
                    lg={6}
                    xl={6}
                  >
                    {getProjectPhoto(p.name, p.img)}
                  </Grid>
                  <Grid container item spacing={1} sx={{ marginTop: "20px" }}>
                    {p.technologies.map((tech) => (
                      <Grid key={"projects-exp-" + tech} item>
                        <Chip label={tech} />
                      </Grid>
                    ))}
                  </Grid>
                </Grid>
              </Paper>
            </Grid>
          ))}
        {(!props.projects?.length ||
          (props.projects?.length === 0 &&
            props.projectLoadedState !== LoadingState.LOADING)) && (
          <Grid
            key="project-not-found"
            item
            xs={12}
            sm={12}
            md={12}
            lg={12}
            xl={12}
          >
            <p className="not-found-number">404</p>
            <p className="not-found-text">No projects were found</p>
          </Grid>
        )}
        {props.projectLoadedState === LoadingState.LOADING && (
          <Grid
            key="projects-loding"
            item
            xs={12}
            sm={12}
            md={12}
            lg={12}
            xl={12}
            textAlign="center"
          >
            <CircularProgress sx={{ marginY: "100px" }} />
          </Grid>
        )}
      </Grid>
    </div>
  );
};
