import React, { useState } from "react";
import Photo from "../../assets/imgs/photo.png";
import PhotoContact from "../../assets/imgs/photo3.jpeg";
import EmailIcon from "@mui/icons-material/Email";
import CodeIcon from "@mui/icons-material/Code";
import FavoriteIcon from "@mui/icons-material/Favorite";
import DownloadIcon from "@mui/icons-material/Download";
import DoNotDisturbIcon from "@mui/icons-material/DoNotDisturb";
import LinkIcon from "@mui/icons-material/Link";
import { User, contactInfo, socialMedias } from "../../datatypes/bindings";
import { Button, Chip, Grid, Icon, Link } from "@mui/material";
import { CDialog } from "../../shared/components/cdialog/cdialog";
import "./landing.scss";
import { CSnackBar } from "../../shared/components/csnackbar/csnackbar";

interface LandingProps {
  user?: User;
}

export const Landing: React.FunctionComponent<LandingProps> = (
  props: LandingProps
) => {
  const [openContactDialog, setOpenContactDialog] = useState(false);
  const [openContactSnackBar, setOpenContactSnackBar] = useState(false);
  const openResume = () => {
    if (props.user) {
      window.open(props.user.portfolio, "_blank");
    } else {
      setOpenContactSnackBar(true);
    }
  };

  const getPresentationCard = () => {
    return (
      <>
        <Grid
          key="landing-presentation-card-grid"
          item
          xs={12}
          sm={12}
          md={6}
          lg={6}
          xl={6}
          textAlign="center"
        >
          <img src={PhotoContact} alt="photo" width="100%" />
          <p>
            <b>Ren Rodrguez Luquin</b>
          </p>
        </Grid>
      </>
    );
  };

  const getContactInfo = () => {
    return (
      <>
        {contactInfo.map((socialMedia) => (
          <li key="landing-contact-info">
            <h5>{socialMedia.name}</h5>
            <Link
              onClick={() => {
                if (socialMedia.url !== "#") {
                  window.open(socialMedia.url, "_blank");
                }
              }}
            >
              <Icon
                sx={{
                  float: "left",
                  color: "#6aa4df",
                  marginRight: "2px",
                }}
              >
                <LinkIcon />
              </Icon>
              <small>{socialMedia.nickname}</small>
            </Link>
          </li>
        ))}
      </>
    );
  };

  const getContactCard = () => {
    return (
      <>
        <Grid
          key="landing-contact-card-grid"
          item
          xs={12}
          sm={12}
          md={6}
          lg={6}
          xl={6}
        >
          <h4>You can find me on:</h4>
          <nav>
            <ul className="contact-list">{getContactInfo()}</ul>
          </nav>
        </Grid>
      </>
    );
  };

  return (
    <>
      <div className="container">
        <Grid key="landing-grid" container spacing={2}>
          <Grid
            key="landing-left-grid"
            item
            xs={12}
            sm={6}
            md={7}
            lg={7}
            xl={7}
            alignContent="center"
          >
            <h2>Hi everyone! I'm</h2>
            <h1 id="landing-name">Ren Rodrguez L</h1>
            <h3>
              <Chip
                icon={<CodeIcon />}
                className="chip"
                label="Full Stack Developer"
              ></Chip>{" "}
              and{" "}
              <Chip
                icon={<FavoriteIcon />}
                className="chip"
                label="Technology Lover"
              ></Chip>
            </h3>
            <div className="resume-btn-container">
              <Button
                id="resume-btn"
                startIcon={<DownloadIcon />}
                variant="contained"
                size="small"
                onClick={() => openResume()}
                fullWidth
              >
                Resume
              </Button>
            </div>
          </Grid>
          <Grid
            key="landing-photo-grid"
            className="photo-container"
            item
            justifyContent="center"
            xs={12}
            sm={6}
            md={5}
            lg={5}
            xl={5}
          >
            <img id="photo" src={Photo} width="100%" />
          </Grid>
        </Grid>
      </div>
      <div className="social-networks">
        <Grid
          key="landing-social-networks-grid"
          container
          spacing={0}
          width="95%"
          margin="auto"
          alignItems="center"
        >
          <Grid
            key="landing-contact-me-grid"
            item
            container
            xs={6}
            sm={6}
            md={6}
            lg={6}
            xl={6}
            spacing={0}
            justifyContent="flex-start"
          >
            <Button
              startIcon={<EmailIcon />}
              id="contact-me-btn"
              variant="outlined"
              size="small"
              onClick={() => setOpenContactDialog(true)}
            >
              Contact me
            </Button>
          </Grid>
          <Grid
            key="landing-right-grid"
            item
            container
            xs={6}
            sm={6}
            md={6}
            lg={6}
            xl={6}
            spacing={0}
          >
            <Grid
              key="landing-social-medias-grid"
              container
              item
              paddingTop="5px"
              justifyContent="flex-end"
              xs={12}
              sm={12}
              md={12}
              lg={12}
              xl={12}
            >
              {socialMedias.map((sm) => (
                <Grid
                  key={"landing-" + sm.name}
                  container
                  item
                  xs={2}
                  sm={2}
                  md={1}
                  lg={1}
                  xl={1}
                  spacing={0}
                  textAlign="right"
                >
                  <a href={sm.url} target="_blank">
                    <img src={sm.icon} width="22px" />
                  </a>
                </Grid>
              ))}
            </Grid>
          </Grid>
        </Grid>

        <CSnackBar
          isSnackBarOpened={openContactSnackBar}
          setIsSnackBarOpened={setOpenContactSnackBar}
          duration={6000}
          message={
            <Button
              className="snack-message"
              startIcon={<DoNotDisturbIcon className="snack-icon" />}
              disabled
            >
              N<span className="snack-message-span">o resume were found</span>
              <span className="snack-color">!</span>
            </Button>
          }
        />

        <CDialog
          isDialogOpened={openContactDialog}
          setIsDialogOpened={setOpenContactDialog}
          title={<h2>Contact me</h2>}
          message={
            <Grid container spacing={3}>
              {getPresentationCard()}
              {getContactCard()}
            </Grid>
          }
        />
      </div>
    </>
  );
};
